<template>
  <div class="content footerBottom">
    <section>
      <van-swipe :autoplay="18000">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img :src="image" />
        </van-swipe-item>
      </van-swipe>
    </section>
    <section class="workbench">
      <div class="title">
        <div class="line"></div>服务管理
      </div>
      <div class="mean">
        <div
          class="meanList"
          v-for="(item, index) in indexMean"
          :key="index"
          @click="selectMean(index)"
        >
          <template>
            <div
              :class="'meanDiv' + ' ' +[index % 3 === 0 ? 'meanDivOdd':'meanDivEven'] "
              v-if="index <= 3"
            >
              <div class="icoDiv" :style="{'background-color':item.color,'background-image':item.bgColor}">
                <van-icon class="icon" :name="item.icoName" size="30" color="#fff" />
              </div>
              <div class="meanTitle">{{item.title}}</div>
            </div>
            <div class="roundDiv" v-else>
              <div class="icoDiv" :style="{'background-color':item.color,'background-image':item.bgColor}">
                <van-icon class="icon" :name="item.icoName" size="30" color="#fff" />
              </div>
              <div>{{item.title}}</div>
            </div>
          </template>
        </div>
      </div>
    </section>
    <section class="staffService">
      <div class="title">
        <div class="line"></div>员工服务
      </div>
      <div class="serviceLine">
        <router-link
          class="service"
          v-for="(item, index) in serviceMean"
          :key="index"
          :to="item.path"
        >
          <div class="icoDiv" :style="'background:' + item.color">
            <van-icon class="icon" :name="item.icoName" size="23" color="#fff" />
          </div>
          <div>{{item.title}}</div>
        </router-link>
      </div>
    </section>
    <van-dialog
      v-model="show"
      title="用户绑定"
      show-cancel-button
      @confirm="confirm"
      confirm-button-text="去绑定"
    >
      <img src="https://img.yzcdn.cn/vant/apple-3.jpg" />
    </van-dialog>
    <footerBottom :tabbars='this.getRespone.mechanicTabbars'/>
  </div>
</template>

<script>

export default {
  data() {
    return {
      show: false,
      flag: true,
      images: [
        "https://img.yzcdn.cn/vant/apple-1.jpg",
        "https://img.yzcdn.cn/vant/apple-2.jpg"
      ],
      indexMean: [
        { title: "上下钟管理", icoName: "clock-o",color:'#62A8FD',bgColor:'linear-gradient(180deg, #62A8FD 0%, #9E80F3 100%)' },
        { title: "我的预约", icoName: "todo-list-o",color:'#B6B7F8',bgColor:'linear-gradient(180deg, #B6B7F8 0%, #F5ACCC 100%)' },
        { title: "轮钟排班", icoName: "friends-o",color:'#E5EBA4',bgColor:'linear-gradient(180deg, #E5EBA4 0%, #A7DA7C 100%)' },
        { title: "我的订单", icoName: "orders-o",color:'#A2ACF1',bgColor:'linear-gradient(180deg, #A2ACF1 0%, #B4B2D3 100%)' },
        { title: "业绩统计", icoName: "chart-trending-o",color:'#F25555',bgColor:'linear-gradient(180deg, #F25555 0%, #FB72AE 100%)' }
      ],
      serviceMean: [
        {
          title: "公告通知",
          icoName: "volume-o",
          color: "#6CAFF5",
          path: "/notice"
        },
        { title: "员工守则", icoName: "records", color: "#F2795B", path: "" },
        { title: "优秀技师", icoName: "award-o", color: "#FFB84D", path: "/excellentMechanic" },
        { title: "技师种类", icoName: "orders-o", color: "#5ED298", path: "/mechanicType" }
      ]
    };
  },
   mounted() {
    // this.common_api.headTop(this.$route.meta.title, "");
    // this.common_api.powerCode().then(([data, corpId]) => {
    //   const code = data.code;
    //   console.log(code)
    //   if (!this.util.getStore("loginInfo")) {
    //     this.getRespone.getLogin(corpId, code).then(result => {
    //       if (result.code === "THRIDCODE_0002") {
    //         this.show = !this.show;
    //       } else if (result.data != null) {
    //         this.util.setStore("secret", result.data.secret);
    //         this.util.setStore("loginInfo", result.data);
    //       }
    //     });
    //   }
    // });
  },
  methods: {
    confirm() {
      this.$router.push("/login");
    },
    selectMean(id) {
      switch (id) {
        case 0:
          if(this.flag){
            this.$router.push({ path: "/clockManage" });
          } else {
            this.common_vant.openToast('暂无可服务工单')
          }
          break;
        case 1:
          this.$router.push({ path: "/appt" });
          break;
        case 2:
          this.$router.push({ path: "/scheduling" });
          break;
        case 3:
          this.$router.push({ path: "/order" });
          break;
        case 4:
          this.$router.push({ path: "/achieveCensus" });
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  height: calc(100% - 50px);
  overflow: scroll;
  img {
    @include wh(100%, 40%);
  }
  font-size: 0.8rem;
  .workbench {
    @include wh(100%, auto);
    padding: 0.5rem 1rem;
    background-color: $fc;
    .mean {
      @include fw(space-between);
      .roundDiv {
        border: 0.1rem solid $Tsc;
        box-shadow: 0px 3px 8px $Tsc;
        border-radius: 5rem;
        position: absolute;
        background-color: #fff;
        z-index: 2;
        padding: 1rem 1.5rem;
        left: calc(50% - 55px);
        margin-top: -11.5rem;
        .icoDiv {
          margin: auto;
        }
      }
      .meanList {
        flex: 0 0 48%;
        color: $fsize;
      }
      .meanDiv {
        @include fRow(center, center);
        padding: 2rem 0.5rem;
        margin: 0.5rem 0rem;
        border: 0.1rem solid $Tsc;
        box-shadow: 0px 3px 8px #f2f3f5;
      }
      .icoDiv {
        border-radius: 30px;
        background: $blue;
        @include wh(3.2rem, 3.2rem);
        @include fr(center);
        margin-right: 0.5rem;
      }
      .meanDivOdd {
        border-top-right-radius: 2rem;
        border-bottom-left-radius: 2rem;
      }
      .meanDivEven {
        border-top-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
      }
    }
  }
  .staffService {
    background: $fc;
    margin: 0.5rem auto;
    padding: 0.5rem 1rem;
    .serviceLine {
      @include fr(space-between);
      margin-top: 0.8rem;
      .service {
        @include fc(center);
        color: $fColor;
      }
      .icoDiv {
        border-radius: 0.3rem;
        @include wh(2.6rem, 2.6rem);
        @include fr(center);
        margin-bottom: 0.4rem;
      }
    }
  }
  .title {
    @include fRow(flex-start, center);
    margin: 0.5rem 0rem;
    .line {
      @include wh(0.2rem, 1rem);
      background: $blue;
      margin-right: 0.5rem;
    }
  }
  .van-dialog {
    img {
      @include wh(10.2rem, 10.2rem);
      margin: 1rem 0rem;
    }
  }
}
</style>